<script>
  import {mapGetters, mapActions} from "vuex";

  export default {
    name: "OffersList",

    components: {},
    props: [],
    data() {
      return {
        // UX
        isLoading: false,
        querying: true,
      };
    },
    computed: {
      ...mapGetters(['user']),
    },
    mounted() {
      Paddle.Setup({vendor: 101642});
    },
    methods: {
      ...mapActions(['']),

      subscribe(plan) {
        window.Paddle.Checkout.open({
          product: plan,
          email: this.user.email,
          customer_country: this.user.country,
          allowQuantity: false,
          passthrough: `{"user_id": ${this.user.id}}`,
          successCallback: (data) => {
            this.$toast.success(this.$t('thank_you_for_your_purchase'))
            this.$auth.refresh({
              success: (res) => {
                this.$store.dispatch("setUser", res.body.data);
              }
            })
          }
        });
      }
    },
  };
</script>


